import {createRouter, createWebHistory} from 'vue-router';
import login from './views/login'
import home from './views/home'

const routes = [
    {
        path:'/login',
        name: 'login',
        components: {
            main : login
        },
        // meta : {
        //     auth : false
        // }
    },
    {
        path:'/',
        name:'home',
        components: {
            main : home
        },
        // meta : {
        //     auth : true
        // }
    }
]

const router =  createRouter({
    history: createWebHistory(),
    routes
})

export default router